@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.main{display:flex;flex-direction:row;width:100vw}

.navbar{display:flex;flex:1 1;flex-direction:column;background:var(--dark-background);height:100vh;align-items:center;max-width:300px}

.navbar-menu{display:flex;flex-direction:column;margin-top:2em;width:100%}

.navbar-menu-item{display:flex;align-items:center;padding:0.5em;font-size:1.5em;margin-bottom:0.5em;padding-left:1em;width:100%}.navbar-menu-item{color:var(--menu-text-color);text-decoration:none;justify-content:flex-start;height:1em}.navbar-menu-item-text{font-size:0.6em;margin-left:1em}.navbar-menu-item:hover{background-color:var(--menu-background-hover);cursor:pointer;transition:1s}.menu-active{color:var(--menu-text-highlight)}.menu-active>svg,.navbar-menu-item:hover>svg{color:var(--menu-text-highlight);transition:1s}.navbar-menu-item:hover>span{color:var(--light-text-color);transition:1s}

.avatar-container{display:flex;flex-direction:column;align-items:center;margin-top:1em}.avatar-image{height:8em;width:8em;border-radius:50%;transition:1s}.avatar-image:hover{box-shadow:5px 5px 15px 5px #ff8080,-9px 5px 10px 5px #ffe488,-7px -5px 15px 5px #8cff85,12px -5px 15px 5px #80c7ff,12px 10px 15px 7px #e488ff,-10px 10px 15px 7px #ff616b,-10px -7px 27px 1px #8e5cff,5px 5px 15px 5px rgba(0,0,0,0)}.avatar-name{font-size:1.2em;font-weight:bolder;color:var(--light-text-color)}

.social{display:flex;width:80%;align-items:center;justify-content:space-between}

.social-item{display:flex;align-items:center;justify-content:center;background-color:var(--social-links);height:2rem;width:2rem;border-radius:50%;transition:0.3s}.social-item:hover{background-color:var(--social-links-highlight)}.social-item>a{color:var(--light-text-color);text-decoration:none;height:1em}

.view{flex:3 1}

.about-div{margin:5em}.about-avatar{display:flex;height:15em;width:15em}.container{display:flex;justify-content:space-between;margin-right:1em}.about-details-list{font-size:0.6em;width:100%}.about-details-title{margin:0;margin-bottom:1em;font-size:1.5em;color:var(--dark-text-color)}.about-details-list-container{display:flex;flex-direction:row}.about-details-list{list-style-type:none;padding-left:0;margin-right:5em}.about-details-list>li{margin-bottom:2em}li{display:flex;align-items:center;list-style-type:none;height:1em}

.section-title{font-size:2em;font-weight:bold;position:relative;color:#173b6c}.section-title::after{margin-top:0.3em;content:'';display:block;width:2em;height:3px;background:#149ddd;bottom:0;left:0}

.contact-div{margin:5em}

.curriculum-div{margin:5em}

.portfolio-div{margin:5em}.portfolio-link{color:--var(--menu-text-highlight);text-decoration:none}

.home{display:flex;background-image:url(../../static/media/home-bg.1b16acbc.png);flex-direction:column;align-items:flex-end;justify-content:center;padding-right:30vh;background-attachment:fixed;background-position:center;background-repeat:no-repeat;background-size:cover;height:100vh}.home-name{color:var(--light-text-color);font-size:2.5em;font-weight:bolder}.home-label{color:var(--light-text-color)}.home-label-text,.typed-cursor{font-size:1.5em}

:root{--dark-background: #040b14;--light-background: #ffffff;--light-blue-background: #f5f8fd;--light-blue-highlight: #149ddd;--light-text-color: #fff;--dark-text-color: #173b6c;--menu-background-hover: rgba(255, 255, 255, 0.1);--menu-text-color: rgba(205, 205, 212, 0.8);--menu-text-highlight: #149ddd;--social-links: #212431;--social-links-highlight: #149ddd;--scroll-bar-track-color: rgba(0, 0, 0, 0.1);--scroll-bar-color: rgba(0, 0, 0, 0.4)}*{font-family:'Roboto', sans-serif;font-size:20px}body{margin:0}

